import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Col, Row } from 'reactstrap';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, InputType, WindowComponent } from 'summer';
import { CLASS_NAME, ValorContabilMLF } from '../../../../models/dtos/mlf/valorContabilMLF';
import { TIPO_VALOR_CONTABIL_ENUM_VALUES } from '../../../../models/enumerated/mlf/tipoValorContabilEnum';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';

interface ValorContabilMLFEditViewProps extends EditViewProps<ValorContabilMLF> {}

const ValorContabilMLFEditView: FC<ValorContabilMLFEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const formProps = useForm<ValorContabilMLF>({
    defaultValues: {
      empresa: globalReducer.globalParameter.empresa,
      ...selectedItem,
    },
    mode: 'onBlur',
  });
  return (
    <WindowComponent title="Manutenção de Valores Contábeis">
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME}>
        <InputGroupInline formProps={formProps} id="empresa.id" hidden={true} />
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true } }}
              id="referencia"
              isPrependLabel={true}
              inputProps={{ autoFocus: true }}
              label="Referência"
              type={InputType.REFERENCE}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true, min: 0.01, maxLength: 15 } }}
              id="valor"
              isPrependLabel={true}
              label="Valor"
              type={InputType.NUMBER}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true } }}
              id="tipoInt"
              isPrependLabel={true}
              label="Tipo"
              type={InputType.DROP_DOWN}
              inputDropDownProps={{
                optionsProvider: TIPO_VALOR_CONTABIL_ENUM_VALUES,
                selectProps: { isClearable: false },
              }}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { maxLength: 300 } }}
              id="observacao"
              isPrependLabel={true}
              label="Observação"
              placeholder="Informe uma Observação"
              type={InputType.TEXT_AREA}
            />
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default ValorContabilMLFEditView;
